@import "./_reset.scss";
////////////////////////////////////////////////////////////////////////
$main-color: #f5ec45;
$title_color: #024059;

div.col-md-6 {
  h4 {
    color: $title_color;
    margin: 0 auto;
    text-align: center;
    padding: 0;
  }
  p {
    color: $title_color;
  }
}

.my-alert {
  background: #eeeeee;
  padding: 15px;
  border-radius: 5px;
  width: 80%;
  margin: auto;
  margin-top: 10px;
  z-index: 2;
}
.my-alert p {
  margin-bottom: 0;
}
