@import "./reset.scss";

.item_content {
  a {
    text-decoration: none;
    text-decoration-line: none;
  }

  h4 {
    color: black;
  }
}

.link_detail {
  text-decoration-line: none;
  p {
    color: black;
  }
  h4:hover {
    color: gray;
    transition-property: font-size;
    transform: scale(1.1);
    transition: 0.5s;
  }
}
.btn {
  margin: 5px 0;
}
